'use strict';

import React from 'react';
import { Link } from 'react-router';

class PresentationComponent extends React.Component {

    render() {
        return (
            <div className="step-1">
                <p>Efter 21 r med den samme ligastruktur er det fra ssonen 2016/17 tid til at prve noget nyt i den danske Superliga. I stedet for den 'skve' struktur med 33 kampe og den hje risiko for nedrykning, indfres nu et slutspil efter 26 kampe  og nok s vsentligt udvides rkken med to hold.</p>
                <p>Den nye struktur har skabt <a href="http://www.bt.dk/fodbold/hjulmand-sabler-ny-superliga-struktur-jeg-tror-ikke-en-skid-paa-den">en del debat</a>, formentlig fordi trnere, spillere, journalister og ikke mindst fans har svrt ved at forst, hvordan tingene kommer til at foreg. Det vil denne hjemmeside forsge at hjlpe p. S klik videre, hvis du gerne vil blive klogere p afviklingen af den kommende fodboldsson i landets bedste rkke.</p>
                <p className="button">
                    <Link className="button" to={'stage1-intro'}>St i gang</Link>
                </p>
            </div>
        );
    }
}

PresentationComponent.displayName = 'PresentationComponent';

export default PresentationComponent;



