'use strict';

import React from 'react';
import { Link } from 'react-router';
var Loading = require('react-loading');

class Stage3SimulatedRelegationComponent extends React.Component {

    constructor(props) {
        super(props);

        this.state = {
            simulating: true
        }
    }

    componentDidMount() {
        setTimeout(() => {
            this.setState({
                simulating: false
            });
        }, 5000);
    }

    render() {

        const semi1 = this.props.teams.stage3Rel1Simulated;
        const semi2 = this.props.teams.stage3Rel2Simulated;
        const winner = this.props.teams.stage3Rel3Simulated;
        const loser = this.props.teams.stage3Rel4Simulated;
        const firstDiv3 = this.props.teams.stage3Rel5Simulated;
        const firstDiv2 = this.props.teams.stage3Rel6Simulated;

        const renderTeams = (teams) => {
            return (
                teams.map((t) => {
                    return (
                        <tr key={t.ID}>
                            <td>{t.Name}</td>
                            <td>{t.W + t.D + t.L}</td>
                            <td>{t.W}</td>
                            <td>{t.D}</td>
                            <td>{t.L}</td>
                        </tr>
                    );
                })
            );
        };

        if (this.state.simulating) {
            return (
                <div className="step-7 simulating">
                    <h3>Simulerer nedrykningsspillet  vent venligst!</h3>
                    <Loading type='bars' color='#fff' />
                </div>
            );
        }

        return (
            <div className="step-6">
                <h2>Fase 3 - Nedrykningsspillet</h2>
                <h3>Semifinaler:</h3>
                <p>Ogs i nedrykningsspillet mdes de fire hold i to semifinaler over to kampe  igen m det forventes, at reglen om udebaneml glder, og at det hjst rangerede hold har hjemmebane i den sidste kamp.</p>

                <table className="teams">
                    <thead>
                        <tr>
                            <th>Hold</th>
                            <th>K</th>
                            <th>V</th>
                            <th>U</th>
                            <th>T</th>
                        </tr>
                    </thead>
                    <tbody className="rel-cup">
                        {renderTeams(semi1, false)}
                    </tbody>
                </table>

                <table className="teams">
                    <thead>
                        <tr>
                            <th>Hold</th>
                            <th>K</th>
                            <th>V</th>
                            <th>U</th>
                            <th>T</th>
                        </tr>
                    </thead>
                    <tbody className="rel-cup">
                        {renderTeams(semi2, false)}
                    </tbody>
                </table>

                <h3>Vinderfinale:</h3>
                <p>Vinderne af de to semifinaler mdes herefter i et opgr over to kampe. Vinderen af dette opgr sikrer sig overlevelse i Superligaen, mens taberen fortsat m holde spndingen ud.</p>
                <table className="teams">
                    <thead>
                        <tr>
                            <th>Hold</th>
                            <th>K</th>
                            <th>V</th>
                            <th>U</th>
                            <th>T</th>
                        </tr>
                    </thead>
                    <tbody className="rel-cup">
                        {renderTeams(winner, false)}
                    </tbody>
                </table>

                <h3>Taberfinale:</h3>

                <p>De to tabere af semifinalerne mdes i det ultimative nedrykkeropgr  taberen ryger direkte i 1. division, mens vinderen fr endnu en chance for overlevelse.</p>

                <table className="teams">
                    <thead>
                        <tr>
                            <th>Hold</th>
                            <th>K</th>
                            <th>V</th>
                            <th>U</th>
                            <th>T</th>
                        </tr>
                    </thead>
                    <tbody className="rel-cup">
                        {renderTeams(loser, true)}
                    </tbody>
                </table>

                <p>I 1. division har flgende hold placeret sig p de tre verste pladser: {this.props.teams.firstDiv[0].Name}, {this.props.teams.firstDiv[1].Name} og {this.props.teams.firstDiv[2].Name}. Her rykker frstnvnte direkte op, mens de to vrige skal spille kvalifikation mod de to tilbagevrende Superligahold.</p>

                <h3>Kvalifikationskamp mellem taberen af vinderfinalen og nr. 3 fra 1. division</h3>

                <p>Taberen af vinderfinalen mder det drligst placerede hold fra 1. division  vinderen fr en plads i Superligaen og taberen rykker ned (eller forbliver nede) i 1. division.</p>

                <table className="teams">
                    <thead>
                        <tr>
                            <th>Hold</th>
                            <th>K</th>
                            <th>V</th>
                            <th>U</th>
                            <th>T</th>
                        </tr>
                    </thead>
                    <tbody className="rel-cup">
                        {renderTeams(firstDiv3, true)}
                    </tbody>
                </table>

                <h3>Kvalifikationskamp mellem vinderen af taberfinalen og nr. 2 fra 1. division</h3>

                <p>Vinderen af taberfinalen fr fornjelsen af det hjst placerede hold fra 1. division i en direkte duel om en plads i Superligaen.</p>

                <table className="teams">
                    <thead>
                        <tr>
                            <th>Hold</th>
                            <th>K</th>
                            <th>V</th>
                            <th>U</th>
                            <th>T</th>
                        </tr>
                    </thead>
                    <tbody className="rel-cup">
                        {renderTeams(firstDiv2, true)}
                    </tbody>
                </table>

                <p>S i ovenstende scenarier er {this.props.teams.stage3Rel4Simulated[1].Name} rykket direkte ned, mens {this.props.teams.stage3Rel5Simulated[1].Name} og {this.props.teams.stage3Rel6Simulated[1].Name} som tabere af de to kvalifikationsopgr ogs fr plads i 1. division i den efterflgende sson.</p>

                <p className="button">
                    <Link to={'faq'}>Opsummering og ofte stillede sprgsml</Link>
                </p>

            </div>
        );
    }
}

Stage3SimulatedRelegationComponent.displayName = 'Stage3SimulatedRelegationComponent';

export default Stage3SimulatedRelegationComponent;



